// Types
const ITEM_DETECTED = "cart/ITEM_DETECTED";
const CANCEL_DETECTED = "cart/CANCEL_DETECTED";
const ADD_ITEM = "cart/ADD_ITEM";
const DELETE_ITEM = "cart/DELETE_ITEM";
const INCREASE_ITEM = "cart/INCREASE_ITEM";
const DECREASE_ITEM = "cart/DECREASE_ITEM";
const PAY = "cart/PAY";

// Actions
export const itemDetected = item => ({
  type: ITEM_DETECTED,
  payload: item
});

export const cancelDetected = () => ({
  type: CANCEL_DETECTED
});

export const addItem = amount => ({
  type: ADD_ITEM,
  payload: amount
});

export const deleteItem = id => ({
  type: DELETE_ITEM,
  payload: id
});

export const increaseItem = id => ({
  type: INCREASE_ITEM,
  payload: id
});

export const decreaseItem = id => ({
  type: DECREASE_ITEM,
  payload: id
});

export const pay = date => ({
  type: PAY,
  payload: date
});

const initialState = {
  detectedItem: false,
  items: [
    {
      id: 880473500023,
      name: "Galaxy Watch 42mm (Bluetooth)",
      price: 339900,
      amount: 1
    }
  ],
  history: {
    date: "No Purchase History",
    items: []
  }
};

// Reducer
const view = (state = initialState, action) => {
  switch (action.type) {
    case ITEM_DETECTED:
      return {
        ...state,
        detectedItem: action.payload
      };
    case CANCEL_DETECTED:
      return {
        ...state,
        detectedItem: false
      };
    case ADD_ITEM:
      const detectedItem = state.detectedItem;
      if (detectedItem === false) return state;

      const itemIdx = state.items.findIndex(item => detectedItem.id);

      return {
        ...state,
        items:
          itemIdx === -1
            ? state.items.concat({
                ...detectedItem,
                amount: action.payload
              })
            : state.items.map(item => {
                if (item.id !== detectedItem.id) return item;
                else
                  return {
                    ...item,
                    amount: item.amount + action.payload
                  };
              })
      };
    case DELETE_ITEM:
      return {
        ...state,
        items: state.items.filter(item => item.id !== action.payload)
      };
    case INCREASE_ITEM:
      return {
        ...state,
        items: state.items.map(item =>
          item.id !== action.payload
            ? item
            : {
                ...item,
                amount: item.amount + 1
              }
        )
      };
    case DECREASE_ITEM:
      return {
        ...state,
        items: state.items.map(item =>
          item.id !== action.payload
            ? item
            : {
                ...item,
                amount: item.amount - 1
              }
        )
      };
    case PAY:
      return {
        ...state,
        items: [],
        history: {
          date: action.payload,
          items: state.items
        }
      };
    default:
      return state;
  }
};

export default view;
